.sell-old-device {
  margin: 40px auto;
  border-radius: 10px;
  padding: 40px 20px;
  box-shadow: 0px 0px 15px 5px #eeeeee;
  -webkit-box-shadow: 0px 0px 15px 5px #eeeeee;
  -moz-box-shadow: 0px 0px 15px 5px #eeeeee;
  background: #fafafa;

  .content-wrapper {
    transform: translateY(0);

    .content-heading {
      font-size: calc(1.3rem + (45 - 22) * ((100vw - 320px) / (1920 - 320)));
      font-family: "Work-Sans-Regular";
      // font-size: 2rem;
      line-height: 1;
      margin-bottom: 20px;
    }
    .benifits {
      font-family: "Work-Sans-Light";
      font-size: 1rem;
      line-height: 1rem;
      margin-right: 10px;
    }
    .select-device-text {
      font-family: "Work-Sans-Semibold";
      font-size: calc(1rem + (45 - 22) * ((100vw - 320px) / (1920 - 320)));
      // font-size: 1.6rem;
    }
  }
  .append-tick > .bi {
    color: #0085c1;
    margin-right: 5px;
    font-size: 1.1rem;
  }

  .select-gadgets-wrapper {
    // justify-content: space-between;
    a {
      text-decoration: none;
      text-align: center;
      .device-container {
        padding: 20px;
        margin: 1rem;
        margin-left: 0;
        border-radius: 10px;
        box-shadow: 0px 0px 15px 5px #d3d3d3;
        -webkit-box-shadow: 0px 0px 15px 5px #d3d3d3;
        -moz-box-shadow: 0px 0px 15px 5px #d3d3d3;

        .device-label {
          align-items: center;
          font-family: "Work-Sans-Regular";
          i {
            margin-right: 5px;
            background: linear-gradient(
              to right,
              #042b46,
              #005879,
              #008aa8,
              #00bfd0,
              #32f6ed
            );
            -webkit-background-clip: text;
            -webkit-text-fill-color: transparent;
          }
          span {
            color: black;
          }
        }
      }
    }
    a:hover {
      text-decoration: none;
    }
  }
}

.select-brand {
  margin-top: 3rem;
  section {
    .select-brand-heading {
      font-size: 1.5rem;
      font-family: "Work-Sans-Regular";
    }

    .select-brand-container {
      display: flex;
      flex-wrap: wrap;
      .individual-brand-container {
        box-shadow: 0px 0px 15px 5px #d3d3d3;
        -webkit-box-shadow: 0px 0px 15px 5px #d3d3d3;
        -moz-box-shadow: 0px 0px 15px 5px #d3d3d3;
        display: flex;
        margin: 15px 10px;
        width: 100px;
        border-radius: 15px;
        text-align: center;
        align-items: center;
        .individual-brand {
          cursor: pointer;
          .individual-brand-wrapper {
            margin: 10px 0;
            .individual-brand-img {
              width: 90%;
            }
          }
        }
      }
    }
  }
}

.question-component-container {
  margin: 40px auto;
  border-radius: 10px;
  padding: 40px 20px;
  box-shadow: 0px 0px 15px 5px #eeeeee;
  -webkit-box-shadow: 0px 0px 15px 5px #eeeeee;
  -moz-box-shadow: 0px 0px 15px 5px #eeeeee;
  background: #fafafa;
  .question-component {
    display: flex;
    flex-direction: column;
    vertical-align: middle;
    justify-content: center;

    .admin-contact-text {
      font-family: "Work-Sans-Italic";
    }
    .mobile-number-wrapper {
      font-family: "Work-Sans-Italic";
      margin-top: 15px !important;
      .mobile-number-field {
        margin-top: 5px !important;
        font-family: "Work-Sans-Regular";
        width: 130px;
        border-radius: 5px;
        border: 1px solid #0085c1;
        display: block;
      }
    }
  }
  .question-image-container {
    .device-details {
      display: flex;
      flex-direction: row;
      font-family: "Work-Sans-Regular";
      .details-label {
        min-width: 160px;
        word-wrap: break-word;
      }
      .details-value {
        margin-left: 10px;
        word-wrap: break-word;
      }
    }
  }
  .device-name {
    font-size: 2rem;
    margin-bottom: 10px !important;
    font-family: "Work-Sans-Regular";
    color: #1082c0;
  }
  .question-text {
    font-size: 1rem;
    font-family: "Work-Sans-Regular";
    .quote-text {
      font-size: 1.5rem;
      .price-quote {
        font-size: 2rem;
        color: #ff6565;
      }
      .device-count {
        color: #2fad27;
      }
    }
  }
  .answer-list-wrapper {
    list-style-type: none;
    padding: 0;
    display: flex;

    justify-content: flex-start;
    .answer-option {
      box-shadow: 0px 0px 15px 1px #bcbcbc;
      -webkit-box-shadow: 0px 0px 15px 1px #bcbcbc;
      -moz-box-shadow: 0px 0px 15px 1px #bcbcbc;
      padding: 10px 20px;
      margin-right: 20px;
      border-radius: 10px;
      font-family: "Work-Sans-Regular";
      cursor: pointer;

      display: flex;
      flex-direction: row;
      align-items: center;
      input {
        margin-right: 10px;
      }
      &:hover {
        box-shadow: 0px 0px 15px 5px #d4daeb;
        -webkit-box-shadow: 0px 0px 15px 5px #d4daeb;
        -moz-box-shadow: 0px 0px 15px 5px #d4daeb;
      }
    }
  }
  .prev-next-question {
    justify-content: space-between;
    a {
      font-family: "Work-Sans-Regular";
    }
    .next-question-wrapper {
      margin-right: 20px;
      .next-question {
        padding: 10px 20px;
        background: #1082c0;
        color: #fff;
        border-radius: 5px;
      }
    }
  }
}

@media (min-width: 320px) {
  /* smartphones, iPhone, portrait 480x320 phones */
  .sell-old-mobile {
    .content-wrapper {
      font-size: 1.5rem;
      .select-gadgets-wrapper {
        .bi {
          font-size: 2rem;
        }
      }
    }
  }
  .select-brand-container {
    justify-content: space-evenly;
  }
}
@media (min-width: 481px) {
  /* portrait e-readers (Nook/Kindle), smaller tablets @ 600 or @ 640 wide. */
}
@media (min-width: 641px) {
  /* portrait tablets, portrait iPad, landscape e-readers, landscape 800x480 or 854x480 phones */
  .sell-old-mobile {
    .content-wrapper {
      .select-gadgets-wrapper {
        a {
          .bi {
            font-size: 1.5rem;
          }
        }
      }
    }
  }
  .select-brand-container {
    justify-content: normal;
  }
}
@media (min-width: 961px) {
  /* tablet, landscape iPad, lo-res laptops ands desktops */
}
